<template>
    <div id="background">
        <div id="window">
            <Spinner v-if="reactive_type == 'progress'" id="spinner"/>
            <img v-else id="image" :src="image" alt="">
            <div id="title">{{ title }}</div>
            <div id="content">{{ content }}</div>
        </div>
    </div>
</template>

<script>
import Spinner from '@/components/Spinner'

export default {
    name: 'Dialog',
    props: [
        'title',
        'content',
        'type'
    ],
    components: {
        Spinner
    },
    data() {
        return {
            image: '',
            reactive_type: this.type
        }
    },
    watch: {
        type: function(new_value, old_value) {
            this.reactive_type = new_value;
            switch (this.reactive_type) {
            case 'success':
                this.image = require('../assets/success.svg');
                break;
            case 'fail':
                this.image = require('../assets/fail.svg');
                break;
            default:
                break;
            }
        }
    },
    mounted() {
        switch (this.reactive_type) {
        case 'success':
            this.image = require('../assets/success.svg');
            break;
        case 'fail':
            this.image = require('../assets/fail.svg');
            break;
        default:
            break;
        }
    }
}
</script>

<style scoped>


#background {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: #00000077;
}

#window {
    position: absolute;
    background-color: white;
    border-radius: 5px;
    width: 70%;
    height: 50%;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

#title {
    position: absolute;
    top: 55%;
    width: 80%;
    margin-left: 10%;
    font-size: 25px;
    text-align: center;
    font-weight: bold;
}

#content {
    position: absolute;
    top: 70%;
    width: 80%;
    margin-left: 10%;
    font-size: 20px;
    text-align: center;
}

#spinner, #image {
    position: absolute;
    width: 50%;
    height: 50%;
    top: 30%;
    left: 50%;
    transform: translate(-50%, -50%);
}

</style>
