<template>
    <div class="signin">
        <div id="header">
            <img id="logo" :src="require('../assets/logo.svg')" alt="" />
            <div id="title">{{ title }}</div>
        </div>
        <div id="form">
            <Input v-if="register" class="input" type="text" :icon="require('../assets/user.svg')" label="Usuario" v-model="display_name"/>
            <Input class="input" type="email" :icon="require('../assets/email.svg')" label="Correo electrónico" v-model="email"/>
            <Input class="input" type="password" :icon="require('../assets/password.svg')" label="Contraseña" v-model="password"/>
            <Input v-if="register" class="input" type="password" :icon="require('../assets/password.svg')" label="Confirmar contraseña" v-model="password2"/>
        </div>
        <div>
            <div v-if="register" class="button" @click="signup">Registrar</div>
            <div v-else class="button" @click="signin">Acceder</div>

            <div v-if="register" class="note">¿Ya tienes cuenta? <b @click="() => {register = !register; this.title='Acceso'}">Ingresa ...</b></div>
            <div v-else class="note">¿No tienes cuenta? <b @click="() => {register = !register; this.title='Registro'}">Crea una ...</b></div>
        </div>

        <Dialog v-if="dialog" :title="dialog_title" :content="dialog_message" :type="dialog_type" />
    </div>
</template>

<script>
import firebase from 'firebase'
import Input from '@/components/Input'
import Dialog from '@/components/Dialog'

export default {
    name: 'Signin',
    components: {
        Input,
        Dialog
    },
    data: function() {
        return {
            title: 'Acceso',
            display_name: null,
            email: null,
            password: null,
            password2: null,
            register: false,
            dialog: false,
            dialog_title: '',
            dialog_type: '',
            dialog_message: ''
        }
    },
    methods: {
        validate() {
            if(!this.email) {
                this.dialog = true;
                this.dialog_type = 'fail';
                this.dialog_title = 'Error';
                this.dialog_message = 'El correo electrónico no puede estar vacío';
            } else if(!this.password) {
                this.dialog = true;
                this.dialog_type = 'fail';
                this.dialog_title = 'Error';
                this.dialog_message = 'La contraseña no puede estar vacía';
            } else if(this.register) {
                if(!this.display_name) {
                    this.dialog = true;
                    this.dialog_type = 'fail';
                    this.dialog_title = 'Error';
                    this.dialog_message = 'El usuario no puede estar vacío';
                } else if(this.password != this.password2) {
                    this.dialog = true;
                    this.dialog_type = 'fail';
                    this.dialog_title = 'Error';
                    this.dialog_message = 'Las contraseñas no coinciden';
                }
            }
            if(this.dialog) {
                setTimeout(() => {
                    this.dialog = false;
                    this.dialog_title = '';
                    this.dialog_message = '';
                }, 2000);
            }
            
            return this.dialog;
        },
        signin() {            
            if(this.validate()) {
                return;
            }
            
            this.dialog = true;
            this.dialog_type = 'progress';
            this.dialog_title = 'Validando';
            this.dialog_message = 'Validando identidad por favor espere';
            firebase.auth().signInWithEmailAndPassword(this.email, this.password).then(
                (user) => {
                    this.$router.replace({name: 'main_menu'})
                },
                (error) => {
                    this.dialog = true;
                    this.dialog_type = 'fail';
                    this.dialog_title = 'Error';
                    this.dialog_message = 'Correo electrónico o contraseña inválidos';
                    if(this.dialog) {
                        setTimeout(() => {
                            this.dialog = false;
                            this.dialog_title = '';
                            this.dialog_message = '';
                        }, 2000);
                    }
                }
            )
        },
        signup() {
            if(this.validate()) {
                return;
            }
            this.dialog = true;
            this.dialog_type = 'progress';
            this.dialog_title = 'Registrando';
            this.dialog_message = 'Creando nuevo usuario por favor espere';
            firebase.auth().createUserWithEmailAndPassword(this.email, this.password)
            .then(this.create_user,
                (error) => {
                    this.dialog = true;
                    this.dialog_type = 'fail';
                    this.dialog_title = 'Error';
                    this.dialog_message = 'Error al registrar, por favor intente más tarde';
                    if(this.dialog) {
                        setTimeout(() => {
                            this.dialog = false;
                            this.dialog_title = '';
                            this.dialog_message = '';
                        }, 2000);
                    }
                }
            )
        },
        create_user(data) {
            let user = firebase.auth().currentUser;

            user.updateProfile({
                displayName: this.display_name,
            }).then(() => {
                this.$router.replace({name: 'main_menu'})
            }).catch((error) => {
                console.log(error);
            });
        }
    }
}
</script>

<style scoped>
.signin {
    width: 100%;
    height: 100%;
    display: grid;
    grid-template-rows: 42px 55% 30%;
}

#header {
    width: 100;
    height: 100%;
    background: linear-gradient(135deg, #67fed7 0%,#0fabf9 100%);
    position: relative;
}

#form {
    margin-left: 5%;
    margin-right: 5%;
    align-self: center;
}

#title {
    position: absolute;
    top: 8px;
    width: 100%;
    text-align: center;
    font-size: 25px;
    color: white;
    font-weight: bold;
}

img {
    width: 32px;
    height: 32px;
    margin: 5px;
}

.button {
    text-align: center;
    background: linear-gradient(135deg, #67fed7 0%,#0fabf9 100%);
    margin-top: 5%; 
    margin-left: 25%;
    margin-right: 25%;
    border-radius: 20px;
    font-size: 20px;
    color: white;
    box-shadow: 0 3px 6px rgba(57,211,232,0.16), 0 3px 6px rgba(57,211,232,0.23);
    height: 40px;
    line-height: 40px;
}

.note {
    text-align: center;
    color: #B5ABAB;
    margin: 3%;
}

@media (min-height: 600px) {
    .signin {
        grid-template-rows: 45% 40% 20%;
    }
    img {
        position: absolute;
        top: 50%;
        left: 50%;
        width: auto;
        height: auto;
        transform: translate(-50%, -50%);
    }
    #title {
        position: absolute;
        top: 85%;
        width: 100%;
        text-align: center;
        font-size: 25px;
        color: white;
        font-weight: bold;
    }
}
</style>
