<template>
    <div id="spinner">
        <img id="outter" :src="require('../assets/spinner_1.svg')" alt="" />
        <img id="inner" :src="require('../assets/spinner_2.svg')" alt="" />
    </div>
</template>

<script>
export default {
    name: 'Spinner'
}
</script>

<style scoped>
#spinner {
    height: 100%;
    width: 100%;
    position: relative;
}

#outter {
    animation: rightspin 3s linear infinite;
    position: absolute;
    top: 0%;
    left: 0%;
    height: 100%;
    width: 100%;
}

#inner {
    animation: leftspin 3s linear infinite;
    position: absolute;
    top: 15%;
    left: 15%;
    height: 70%;
    width: 70%;
}

@keyframes rightspin { 100% {transform:rotate(360deg); } }
@keyframes leftspin { 100% {transform:rotate(-360deg); } }
</style>
