<template>
    <div class="group">
        <input ref="inp" :type="type" required="required" :value="value" @input="$emit('input', $event.target.value)" />
        <img :src="icon" alt="">
        <span class="highlight"></span>
        <span class="bar"></span>
        <label>{{ label }}</label>
    </div>
</template>

<script>
export default {
    name: 'Input',
    props: [
        'label',
        'type',
        'icon',
        'value'
    ]
}
</script>

<style scoped>

.group {
    position: relative;
}

textarea {
  resize: none;
}

input {
  background: none;
  color:black;
  font-size: 20px;
  font-weight: bold;
  font-family: 'Roboto', sans-serif;  
  padding: 10px 10px 3px 5px;
  display: block;
  width: 95%;
  border: none;
  border-radius: 0;
  border-bottom: 1px solid #c6c6c6;
  margin-top: 8%;
}
input:focus {
  outline: none;
}
input:focus ~ label, input:valid ~ label {
  top: -14px;
  font-size: 12px;
  color: #2196F3;
}
input:focus ~ .bar:before {
  width: 100%;
}

input[type="password"] {
  letter-spacing: 0.3em;
}

label {
  color: #c6c6c6;
  font-size: 16px;
  position: absolute;
  pointer-events: none;
  left: 5px;
  top: 10px;
  transition: 300ms ease all;
}

.bar {
  position: relative;
  display: block;
  width: 100%;
}
.bar:before {
  content: '';
  height: 2px;
  width: 0;
  bottom: 0px;
  position: absolute;
  background: #2196F3;
  transition: 300ms ease all;
  left: 0%;
}

img {
    position: absolute;
    top: 20%;
    left: 92%;
}

</style>
