import Vue from 'vue'
import Router from 'vue-router'
import Signin from './views/Signin.vue'
import MainMenu from './views/MainMenu.vue'
import ScanQR from './views/ScanQR.vue'
import PaymentDetails from './views/PaymentDetails.vue'
import PaymentAuth from './views/PaymentAuth.vue'
import PaymentComplete from './views/PaymentComplete.vue'
import Error from './views/Error.vue'
import firebase from 'firebase'

Vue.use(Router)

const router =  new Router({
  mode: 'history',
  base: 'QRPayment',
  routes: [
    {
      path: '*',
      component: Signin
    },
    {
      path: '/',
      name: 'signin',
      component: Signin
    },
    {
      path: '/main_menu',
      name: 'main_menu',
      component: MainMenu,
      meta: {
        requiresAuth: true
      }
    },
    {
      path: '/scan_code',
      name: 'scan_code',
      component: ScanQR,
      meta: {
        requiresAuth: true
      }
    },
    {
      path: '/payment_details',
      name: 'payment_details',
      component: PaymentDetails,
      meta: {
        requiresAuth: true
      }
    },
    {
      path: '/payment_auth',
      name: 'payment_auth',
      component: PaymentAuth,
      meta: {
        requiresAuth: true
      }
    },
    {
      path: '/payment_complete',
      name: 'payment_complete',
      component: PaymentComplete,
      meta: {
        requiresAuth: true
      }
    },
    {
      path: '/error/:title/:desc/:image',
      name: 'error',
      component: Error,
      meta: {
        requiresAuth: true
      }
    }
  ]
})

router.beforeEach((to, from, next) => {
  const currentUser = firebase.auth().currentUser;
  const requiresAuth = to.matched.some(record => record.meta.requiresAuth);

  if(requiresAuth && !currentUser) next('signin');
  else if(!requiresAuth && currentUser) next('main_menu');
  else next();
})

export default router;