<template>
    <div class="payment_details">
        <div id="header">
            <img id="back" :src="require('../assets/back.svg')" alt="" @click="$router.back();"/>
            <img id="close" :src="require('../assets/close.svg')" alt="" @click="close"/>
            <img id="logo" :src="require('../assets/bag.svg')" alt=""/>
            <div id="title">{{ title }}</div>
            <div id="content">{{ content }}</div>
        </div>
        <div id="details">
            <Label class="input" type="text" :icon="require('../assets/commerce.svg')" label="Comercio" v-model="commerce"/>
            <Label class="input" type="text" :icon="require('../assets/money.svg')" label="Monto" v-model="amount"/>
            <Label class="input" type="text" :icon="require('../assets/smallbag.svg')" label="Concepto" v-model="concept"/>
            <Label class="input" type="text" :icon="require('../assets/paymethod.svg')" label="Método de pago" v-model="paymethod"/>
        </div>
        <div id="buttons">
            <div class="button" @click="pay">Pagar</div>
        </div>

        <Dialog v-if="modal" :title="dialog_title" :content="dialog_content" :type="process" />

    </div>
</template>

<script>
import Label from '@/components/Label'
import Spinner from '@/components/Spinner'

export default {
    name: 'PaymentDetails',
    data: function() {
        return {
            commerce: this.$store.state.payment.commerce,
            amount: '$ ' + (this.$store.state.payment.amount / 100).toFixed(2),
            concept: this.$store.state.payment.concept,
            paymethod:'****-****-****-7894',
            title: 'Atrás',
            content:'Detalles del pago, presione pagar para aceptar el pago',
            canvas_tag: {},
            canvas: null,
            modal: false,
            process: '',
            dialog_title: '',
            dialog_content: ''
        }
    },
    components: {
        Label,
        Spinner
    },
    mounted: function() {

    },
    methods: {
        pay() {
            this.modal = true;
            this.process = 'progress';
            this.dialog_title = 'Autorizando';
            this.dialog_content = 'Por favor espere un momento';

            setTimeout(this.finish, 6000);

        },
        finish() {
            this.process = 'success';
            this.dialog_title = 'Éxito';
            this.dialog_content = 'Pago realizado correctamente';

            setTimeout(() => this.$router.replace({name: 'main_menu'}), 3000);
        },
        close() {
            
        }
    }
}
</script>

<style scoped>
#qrcode {
    background-color: black;
    width: 100%;
    height: 100%;
}

#canvas {
    width: 100%;
    height: 100%;
}

.payment_details {
    height: 100%;
    width: 100%;
    display: grid;
    grid-template-rows: 40% 45% 15%;
    align-items: center;
}

#header {
    width: 100;
    height: 100%;
    background: linear-gradient(135deg, #67fed7 0%,#0fabf9 100%);
    position: relative;
}

#title {
    position: absolute;
    top: 5%;
    left: 8%;
    width: 20%;
    text-align: center;
    font-size: 20px;
    color: white;
}

#content {
    position: absolute;
    top: 70%;
    width: 70%;
    margin-left: 15%;
    margin-right: 15%;
    text-align: center;
    font-size: 22px;
    color: white;
}

#close {
    position: absolute;
    left: 90%;
    top: 3%;
}

#back {
    position: absolute;
    left: 3%;
    top: 3%;
}

#logo {
    position: absolute;
    top: 40%;
    left: 50%;
    transform: translate(-50%, -50%);
}

#details {
    margin: 5%;
    align-self: center;
}

.button {
    text-align: center;
    background: linear-gradient(135deg, #67fed7 0%,#0fabf9 100%);
    margin-left: 25%;
    margin-right: 25%;
    border-radius: 20px;
    font-size: 22px;
    color: white;
    box-shadow: 0 3px 6px rgba(57,211,232,0.16), 0 3px 6px rgba(57,211,232,0.23);
    height: 40px;
    line-height: 40px;
}


#dialog-background {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: #00000077;
}

#dialog-window {
    position: absolute;
    background-color: white;
    border-radius: 5px;
    width: 70%;
    height: 50%;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

#dialog-title {
    position: absolute;
    top: 55%;
    width: 80%;
    margin-left: 10%;
    font-size: 25px;
    text-align: center;
    font-weight: bold;
}

#dialog-content {
    position: absolute;
    top: 70%;
    width: 80%;
    margin-left: 10%;
    font-size: 20px;
    text-align: center;
}

#spinner, #success {
    position: absolute;
    width: 50%;
    height: 50%;
    top: 30%;
    left: 50%;
    transform: translate(-50%, -50%);
}

</style>
