<template>
    <div id="scan-qr">
        <div id="header">
            <img id="close" :src="require('../assets/close.svg')" alt="" @click="cancel"/>
            <img id="logo" :src="require('../assets/qrlogo.svg')" alt="" />
            <div id="title">{{ title }}</div>
        </div>
        <canvas ref="canvas" id="canvas"></canvas>
        <video ref="video" hidden></video>
    </div>
</template>

<script>

import jsQR from "jsqr";

export default {
    name: 'ScanCode',
    data: function() {
        return {
            video: {},
            canvas_tag: {},
            canvas: null,
            title: 'Enfoca el código QR hasta que se vea claramente'
        }
    },
    mounted: function() {
        this.video = this.$refs.video;
        this.canvas_tag = this.$refs.canvas;
        this.canvas = this.canvas_tag.getContext("2d");

        navigator.mediaDevices.getUserMedia({ video: { facingMode: "environment" } }).then((stream) => {
            this.video.srcObject = stream;
            this.video.setAttribute("playsinline", true);
            this.video.play();
            requestAnimationFrame(this.tick);
        });
    },
    methods: {
        cancel() {
            this.video.src ='';
            this.video.srcObject.getTracks()[0].stop();
            this.$router.back();
        },
        /*drawLine(begin, end, color) {
            this.canvas.beginPath();
            this.canvas.moveTo(begin.x, begin.y);
            this.canvas.lineTo(end.x, end.y);
            this.canvas.lineWidth = 4;
            this.canvas.strokeStyle = color;
            this.canvas.stroke();
        },*/
        tick() {
            if (this.video.readyState === this.video.HAVE_ENOUGH_DATA) {
                //this.canvas_tag.height = this.canvas_tag.width * 1.25;
                //this.canvas_tag.width = this.video.videoWidth;
                this.canvas.drawImage(this.video, 0, 0, this.canvas_tag.width, this.canvas_tag.height);
                let imageData = this.canvas.getImageData(0, 0, this.canvas_tag.width, this.canvas_tag.height);
                let code = jsQR(imageData.data, imageData.width, imageData.height, { inversionAttempts: "dontInvert", });
                
                if (code) {
                    /*this.drawLine(code.location.topLeftCorner, code.location.topRightCorner, "#FF3B58");
                    this.drawLine(code.location.topRightCorner, code.location.bottomRightCorner, "#FF3B58");
                    this.drawLine(code.location.bottomRightCorner, code.location.bottomLeftCorner, "#FF3B58");
                    this.drawLine(code.location.bottomLeftCorner, code.location.topLeftCorner, "#FF3B58");*/

                    let imageData = this.canvas.getImageData(
                        code.location.topLeftCorner.x, 
                        code.location.topLeftCorner.y,
                        code.location.bottomRightCorner.x - code.location.topLeftCorner.x,
                        code.location.bottomRightCorner.y - code.location.topLeftCorner.y
                    );

                    this.video.src ='';
                    this.video.srcObject.getTracks()[0].stop();

                    try {
                        let payment = JSON.parse(code.data)

                        if(payment.commerce && payment.amount && payment.concept) {
                            
                            this.$store.commit('setImage', imageData);
                            this.$store.commit('setPayment', payment);
                            this.$router.push('payment_details');
                        
                            return;
                        } else {
                            console.log(err);
                            this.$router.push({
                                name: 'error',
                                params: {
                                    title: 'QR inválido',
                                    desc: '¿Desea volver a intentar?',
                                    image: require('../assets/warning.svg'),
                                    reject: 'main_menu'
                                }
                            })
                            return;
                        }
                    } catch(err) {
                        console.log(err);
                        this.$router.push({
                            name: 'error',
                            params: {
                                title: 'QR inválido',
                                desc: '¿Desea volver a intentar?',
                                image: require('../assets/warning.svg'),
                                reject: 'main_menu'
                            } 
                        })
                        return;
                    }
                }
            }
            requestAnimationFrame(this.tick);
        }
    }
}
</script>

<style scoped>

#scan-qr {
    width: 100%;
    height: 100%;
    background-color: #37474F;
    display: grid;
    grid-template-rows: 40% 60%;
    align-items: center;
    text-align: center;
}

#header {
    width: 100;
    height: 100%;
    background: linear-gradient(135deg, #67fed7 0%,#0fabf9 100%);
    position: relative;
}

#logo {
    position: absolute;
    top: 40%;
    left: 50%;
    transform: translate(-50%, -50%);
}

#title {
    position: absolute;
    top: 70%;
    width: 70%;
    margin-left: 15%;
    margin-right: 15%;
    text-align: center;
    font-size: 22px;
    color: white;
}

#canvas {
    width: 100%;
    height: 100%;
}

#close {
    position: absolute;
    left: 90%;
    top: 3%;
}

</style>
